<template>
  <div class="vue-uploader">
    <div class="file-list">
      <section v-for="(file, index) of files" class="file-item draggable-item" :key="index">
        <div class="lt">
          <img :src="file.src" ondragstart="return false;">
        </div>
        <div class="rt">
          <p class="file-name">{{file.name}}</p>
          <span class="file-remove" @click="remove(index)">
            <i class="icon icon-add"></i>
          </span>
        </div>
      </section>
      <section v-if="status == 'ready'" class="file-item">
        <div @click="add()" class="add">
          <span>
            <i class="icon icon-add"></i>
          </span>
        </div>
      </section>
    </div>
    <!-- <section v-if="files.length != 0" class="upload-func">
        <div class="progress-bar">
          <section v-if="uploading" :width="(percent * 100) + '%'">{{(percent * 100) + '%'}}</section>
        </div>
        <div class="operation-box">
          <button v-if="status == 'ready'" @click="submit()">上传</button>
          <button v-if="status == 'finished'" @click="finished()">完成</button>
        </div>
      </section> -->
    <input class="flieInput" type="file" accept="image/*,video/*" @change="fileChanged()" ref="file" multiple="multiple">
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    src: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      status: 'ready',
      files: [],
      point: {},
      uploading: false,
      percent: 0
    }
  },
  methods: {
    // 点击添加按钮打开 上传界面
    add() {
      if (this.files.length >= 3) {
        return
      }
      this.$refs.file.click()
    },
    // 发送
    submit() {
      // 没有文件
      if (this.files.length === 0) {
        console.warn('no file!')
        return
      }
      const formData = new FormData()
      this.files.forEach((item) => {
        formData.append(item.name, item.file)
      })
      const xhr = new XMLHttpRequest()
      xhr.upload.addEventListener('progress', this.uploadProgress, false)
      xhr.open('POST', this.src, true)
      this.uploading = true
      xhr.send(formData)
      xhr.onload = () => {
        this.uploading = false
        if (xhr.status === 200 || xhr.status === 304) {
          this.status = 'finished'
          console.log('upload success!')
        } else {
          console.log(`error：error code ${xhr.status}`)
        }
      }
    },
    // 状态 是否完成
    finished() {
      this.files = []
      this.status = 'ready'
    },
    // 移除选中的文件
    remove(index) {
      this.files.splice(index, 1)
    },
    // 文件改变
    fileChanged() {
      const list = this.$refs.file.files
      for (let i = 0; i < list.length; i++) {
        if (i > 3) {
          break
        }
        if (!this.isContain(list[i])) {
          const item = {
            name: list[i].name,
            size: list[i].size,
            file: list[i]
          }
          this.html5Reader(list[i], item)
          this.files.push(item)
        }
      }
      this.$refs.file.value = ''
    },
    // 将图片文件转成BASE64格式
    html5Reader(file, item) {
      const reader = new FileReader()
      reader.onload = (e) => {
        this.$set(item, 'src', e.target.result)
      }
      reader.readAsDataURL(file)
    },
    isContain(file) {
      this.files.forEach((item) => {
        if (item.name === file.name && item.size === file.size) {
          return true
        }
      })
      return false
    },
    uploadProgress(evt) {
      const component = this
      if (evt.lengthComputable) {
        const percentComplete = Math.round((evt.loaded * 100) / evt.total)
        component.percent = percentComplete / 100
      } else {
        console.warn('upload progress unable to compute')
      }
    }
  },
  watch: {
    files(vel) {
      this.$emit('fileChange')
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.vue-uploader
  border 0px solid #e5e5e5
  .file-list
    padding: 0px
    &:after 
      content ''
      display block
      clear both
      visibility hidden
      line-height 0
      height 0
      font-size 0
    .file-item
      position relative
      display flex
      .lt
        flex 0 0 70px
        width 70px
        img
          width 50px
          height 50px
          border-radius 3px
          border 0px solid #ececec
          margin-left 10px
      .rt
        flex 1
        padding-right 20px
        .file-remove
          position absolute
          right 0px
          top 2px
          width 14px
          height 14px
          color #999
          cursor pointer
          line-height 14px
          border-radius 100%
          font-size 24px
          transform rotate(45deg)
        .file-name
          margin 0
          height 40px
          line-height 20px
          word-break break-all
          font-size 12px
          overflow hidden
          text-overflow ellipsis
          display -webkit-box
          -webkit-line-clamp 2
          -webkit-box-orient vertical
  .add 
    width 50px
    height 50px
    margin-left 10px
    text-align center
    color #999
    border 0.5px dashed #999
    border-radius 3px
    font-size 0 
    cursor pointer
    display flex
    justify-content center
    align-items center
    span
      font-size 40px
      font-weight 700
  .upload-func
    display flex
    padding 10px
    margin 0px
    background #f8f8f8
    border-top 1px solid #ececec
    .progress-bar
      flex-grow: 1
      section
        margin-top 5px
        background #00b4aa
        border-radius 3px
        text-align center
        color #fff
        font-size 12px
        transition all .5s ease
      .operation-box
        flex-grow 0
        padding-left 10px
        button
          padding 4px 12px
          color #fff
          background #007ACC
          border none
          border-radius 2px
          cursor pointer
  .flieInput 
    display none
</style>


