import Vue from 'vue'
import Router from 'vue-router'
import Hello from 'components/Hello'
import Queries from 'components/Queries'
import QueriesInfo from 'components/QueriesInfo'
import Problem from 'components/Problem'
import Forms from 'components/Forms'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Hello,
      children: [
        {
          path: '/problem',
          component: Problem,
          children: [
            {
              path: ':id',
              component: Forms
            }
          ]
        },
        {
          path: '/queries',
          component: Queries,
          children: [
            {
              path: ':id',
              component: QueriesInfo
            }
          ]
        }
      ]
    }
  ]
})



