<template>
  <transition enter-active-class="fadeInRight" leave-active-class="fadeOutRight">
    <div class="problem animated">
      <Scroll ref="scroll">
        <div>
          <Background>
            <Heade :data="hdInfo"></Heade>
            <div class="problem-wrapper">
              <ul class="problem-list">
                <li class="list" v-for="(item,index) in dataList" :class="{'no-bot':index===dataList.length-1}" :key="index" @click="openForm(index)">
                  <span class="text">{{index+1}}. {{item}}</span>
                  <div class="rightc">
                    <span></span>
                  </div>
                </li>
              </ul>
            </div>
          </Background>
        </div>
      </Scroll>
      <router-view :data="nowData"></router-view>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
import Heade from 'components/Heade'
import Background from 'components/Background'
import Scroll from 'base/Scroll'

const PROBLEM_LIST = [
  '公款大吃大喝',
  '公款旅游（国内）',
  '公款出国（境）旅游',
  '违规配备使用公车',
  '违规建设搂堂馆所',
  '津贴补贴违规问题',
  '违规收受礼品礼金',
  '大办婚丧喜庆事宜',
  '提供或者接受超标准接待',
  '接受或用公款参与高消费娱乐健身活动',
  '违规出入私人会所',
  '培训疗养机构“四风”问题'
]
// header文本的内容
const TITEL_INFO = {
  big_title: '选择举报内容：'
}
export default {
  components: {
    Heade,
    Background,
    Scroll
  },
  data() {
    return {
      dataList: PROBLEM_LIST,
      nowData: [],
      hdInfo: TITEL_INFO
    }
  },
  methods: {
    openForm(i) {
      this.nowData[0] = this.dataList[i]
      this.$router.push({
        path: `/problem/${i}`
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
.problem
  position fixed
  top 0px
  left 0px
  bottom 0px
  right 0px
  background #fff
  .problem-wrapper
    padding 15px
    .problem-list
      background #fff
      border-radius 0px
      .list
        position relative
        line-height 40px
        padding-right 15px
        font-size 14px
        text-indent 8px
        border-bottom 0.5px solid #e5e5e5
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
        &.no-bot
          border-bottom none
        .rightc
          position absolute
          right 15px
          top 15px
          width 10px
          height 10px
          border-top 0.5px solid #666
          border-right 0.5px solid #666
          transform rotate(45deg)
</style>



