<template>
  <transition enter-active-class="fadeInRight" leave-active-class="fadeOutRight">
    <div class="forms animated">
      <Scroll ref="scroll" :data="data">
        <div>
          <Background>
            <Heade :data="hdInfo"></Heade>
            <div class="form-wrapper">
              <div class="block">
                <div class="lt">
                  <span>问题类别：</span>
                </div>
                <div class="rt">
                  <input type="text" v-model="title" readonly>
                </div>
              </div>
              <div class="block">
                <div class="lt">
                  <span>举报内容：</span>
                  <br>
                  <span class="sp">{{textLength}}</span>
                </div>
                <div class="rt">
                  <textarea minlength="20" maxlength="500" type="text" v-model="input_textarea" rows="4" placeholder="20字以上，500字以内"></textarea>
                </div>
              </div>
              <div class="block">
                <div class="lt">
                  <span>所属区域：</span>
                </div>
                <div class="rt">
                  <select class="select" v-model="select_area">
                    <option value="0">-- 请选择 --</option>
                    <option v-for="(item,index) in area_select" :value="index+1" :key="index">{{item}}</option>
                  </select>
                  <span class="icon icon-keyboard_arrow_down"></span>
                </div>
              </div>
              <div class="block">
                <div class="lt">
                  <span>反应对象：</span>
                </div>
                <div class="rt">
                  <select class="select" v-model="select_mans">
                    <option value="0">-- 请选择 --</option>
                    <option v-for="(item,index) in mans_select" :value="index+1" :key="index">{{item}}</option>
                  </select>
                  <span class="icon icon-keyboard_arrow_down"></span>
                </div>
              </div>
              <div class="block">
                <div class="lt">
                  <span>级别：</span>
                </div>
                <div class="rt">
                  <select class="select" v-model="select_leve">
                    <option value="0">-- 请选择 --</option>
                    <option v-for="(item,index) in leve_select" :value="index+1" :key="index">{{item}}</option>
                  </select>
                  <span class="icon icon-keyboard_arrow_down"></span>
                </div>
              </div>
              <div class="file">
                <div class="selt">
                  <div class="lt">
                    <span>上传附件：</span>
                  </div>
                  <div class="rt">
                    <images-upload ref="images" :src="'/api/imgs'" @fileChange="refresh()"></images-upload>
                  </div>
                </div>
                <div class="read">
                  <span>温馨提示：</span>最多上传4个附件，仅限图片，每个附件不得超过5M
                </div>
              </div>
              <div class="block">
                <div class="lt">
                  <span>验证码：</span>
                </div>
                <div class="rt code">
                  <span @click="code()">{{codeArr[0]}} + {{codeArr[1]}} =</span>
                  <input type="number" placeholder="输入结果" v-model="inputCode">
                </div>
              </div>
              <div class="error_info">
                <div v-if="checked(input_textarea,1)">！举报内容字数过少</div>
                <div v-if="checked(select_area,2)">！请选择举报区域</div>
                <div v-if="checked(select_mans,3)">！请选择举报对象</div>
                <div v-if="checked(inputCode,4)">！验证码不正确</div>
              </div>
              <div class="send" @click="send()">
                <a>提交</a>
              </div>
            </div>
          </Background>
        </div>
      </Scroll>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
import Heade from 'components/Heade'
import Background from 'components/Background'
import Scroll from 'base/Scroll'
import ImagesUpload from 'base/ImagesUpload'

import axios from 'axios'

const AREA_SELECTS = [
  '文艺路街道',
  '朝阳街街道',
  '韭菜园街道',
  '五里牌街道',
  '火星街道',
  '马王堆街道',
  '东屯渡街道',
  '湘湖街道',
  '定王台街道',
  '荷花园街道',
  '东岸街道',
  '马坡岭街道',
  '东湖街道'
]
const MANS_SELECTS = [
  '县（市、区）领导干部',
  '县（市、区）直属机关干部',
  '乡（镇、街道）领导干部',
  '乡（镇、街道）干部',
  '村（居）干部'
]
const LEVE_SELECTS = [
  '正县处级',
  '副县处级',
  '正乡科级',
  '副乡科级',
  '一般干部',
  '军队',
  '金融机构',
  '一般企业',
  '事业',
  '农村',
  '其他'
]
const TITEL_INFO = {
  big_title: '填写举报详情：'
}
export default {
  components: {
    Heade,
    Background,
    Scroll,
    ImagesUpload
  },
  props: {
    data: {
      type: Array
    }
  },
  data() {
    return {
      input_textarea: '',
      codeArr: [],
      inputCode: '',
      select_area: 0,
      select_mans: 0,
      select_leve: 0,
      area_select: AREA_SELECTS,
      mans_select: MANS_SELECTS,
      leve_select: LEVE_SELECTS,
      hdInfo: TITEL_INFO
    }
  },
  created() {
    // 调用验证码
    this.code()
    this.title = this.data[0]
  },
  computed: {
    textLength() {
      let num = this.input_textarea.length
      if (num < 20) {
        return '还差' + (20 - num) + '字'
      } else {
        return '剩余' + (500 - num) + '字'
      }
    }
  },
  methods: {
    look() {
      console.log(this.select_area)
    },
    // 调用Image 组件的 scroll 刷新方法
    refresh() {
      this.$refs.scroll.refresh()
    },
    // 验证码计算
    code() {
      let num1 = Math.floor(Math.random() * 20 + 1)
      let num2 = Math.floor(Math.random() * 20 + 1)
      this.codeArr = [num1, num2]
    },
    // 验证数据
    checked(data, i) {
      if (i === 1) {
        return data.length >= 20 && data.length <= 500 ? 0 : 1
      }
      if (i === 2 || i === 3) {
        return parseInt(data) !== 0 ? 0 : 1
      }
      if (i === 4) {
        return (parseInt(data) !== this.codeArr[0] + this.codeArr[1]) ? 1 : 0
      }
    },
    // 点击发送按钮时验证
    send() {
      if (this.input_textarea < 20 || this.input_textarea > 500) {
        return false
      }
      if (parseInt(this.select_area) === 0 || parseInt(this.select_mans) === 0) {
        return false
      }
      if (parseInt(this.inputCode) !== this.codeArr[0] + this.codeArr[1]) {
        return false
      }
      this.$refs.images.submit()
      // 请求链接
      let url = 'http://localhost:8080'
      // 发送的数据
      let data = {
        title: this.title,
        input_textarea: this.input_textarea,
        select_area: [this.select_area - 1, this.area_select[this.select_area]],
        select_mans: [this.select_mans - 1, this.mans_select[this.select_mans]],
        select_leve: [this.select_leve - 1, this.leve_select[this.select_leve]]
      }
      axios({
        // 静态文件不支持post 所以先使用get 测试
        method: 'get',
        url: url,
        params: data
      })
        .then((res) => {
          console.log(res)
          // 提交成功后 返回 当前 写入数据库的 id 作为查询码
        })
        .catch((err) => {
          console.log(err)
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  .forms
    position fixed
    top 0px
    left 0px
    bottom 0px
    right 0px
    background #FFF;
    .back
      position absolute
      left 5px
      top 5px
      z-index 10
    .form-wrapper
      padding 15px;
      .block
        display flex
        background #fff
        margin-bottom 0px
        border-bottom 0.5px solid #e8e8e8
        padding 10px
        font-size 14px
        line-height 25px
        border-radius 0px
        .lt
          flex 0 0 75px
          width 75px
          font-size 14px
          color #666
          .sp
            color #999
            font-size 10px
        .rt
          flex 1
          position relative
          input
            line-height 25px
            width 100%
            border-radius 3px
          textarea
            margin 0
            padding 0
            width 100%
            border none
            height 100%
            margin-top 4px
          .select
            appearance none
            width 100%
            height 25px
            color #666
            text-indent 5px
            border solid 0.5px #e5e5e5
            background #f6f6f6
            border-radius 3px
          .icon
            position absolute
            right 6px
            top 6px
            font-size 18px
            color #666
          &.code
            display flex
            span
              flex 0 0 65px
            input
              flex 1
              font-size 12px
              background #f6f6f6
              text-indent 5px
      .file
        background #fff
        margin-bottom 0px
        border-bottom 0.5px solid #e8e8e8
        padding 10px
        font-size 14px
        line-height 25px
        border-radius 0px
        .selt
          display flex 
          margin-bottom 10px
          .lt
            flex 0 0 75px
            width 75px
            color #666
          .rt
            flex 1
            .fileBut
              position: relative;
              display: inline-block;
              background: red;
              border-radius: 4px;
              padding: 4px 12px;
              overflow: hidden;
              color: #fff;
              text-decoration: none;
              text-indent: 0;
              line-height: 20px;
              input
                width 100%
                height 100%
                position: absolute;
                font-size: 100px;
                right: 0;
                top: 0;
                opacity: 0;
        .read
          font-size 10px
          line-height 14px
          span
            color red
      .error_info
        height 40px
        line-height 40px
        color red
        text-align center
        font-size 12px
        overflow hidden
      .send
        background red
        display block
        padding 10px
        font-size 16px
        line-height 25px
        border-radius 5px
        text-align center
        a
          color #fff
</style>



