<template>
  <div class="heade">
    <div class="heade-img">这里是图片区域</div>
    <div class="heade-title">
      <div class="big_title">{{bigTitle}}</div>
      <div class="small_title" v-if="smallTitle" >{{smallTitle}}</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    data: {
      type: Object
    }
  },
  data() {
    return {
      bigTitle: this.data.big_title,
      smallTitle: this.data.small_title
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
.heade
  .heade-img
    height 100px
    line-height 100px
    text-align center
    background red
  .heade-title
    padding-top 15px
    font-weight 700
    font-style italic
    color #fff
    text-shadow 2px 2px 3px red
    letter-spacing 1px
    .big_title
      font-size 25px
      line-height 30px
      text-align left
      padding-left 20px
      margin-bottom 5px
    .small_title
      height 30px
      font-size 20px
      text-align right
      padding-right 15px
      line-height 30px
</style>


