<template>
  <transition enter-active-class="fadeInRight" leave-active-class="fadeOutRight">
    <div class="queries animated">
      <Scroll ref="scroll">
        <div>
          <Background>
            <Heade :data="hdInfo"></Heade>
            <div class="queries-wrapper">
              <div class="title">
                <div class="lt">查询码：</div>
                <div class="rt">
                  <input type="number" value="" placeholder="输入查询码">
                </div>
              </div>
              <div class="send" @click="openInfo()">
                <a>查询</a>
              </div>
            </div>
          </Background>
        </div>
      </Scroll>
      <router-view></router-view>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
import Heade from 'components/Heade'
import Background from 'components/Background'
import Scroll from 'base/Scroll'
// header文本的内容
const TITEL_INFO = {
  big_title: '查询处理进度:'
}
export default {
  components: {
    Heade,
    Background,
    Scroll
  },
  data() {
    return {
      hdInfo: TITEL_INFO
    }
  },
  methods: {
    openInfo() {
      this.$router.push({
        path: `/queries/info`
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  .queries
    position absolute
    top 0
    left 0
    bottom 0
    right 0
    background #fff
    .queries-wrapper
      padding 15px 15px 200px 15px
      .title
        display flex
        height 20px
        padding 10px
        line-height 20px
        margin-bottom 20px
        font-size 14px
        background #fff
        .lt
          flex 0 0 70px
        .rt
          flex 1
          input
            width 100%
            line-height 20px
            border-radius 0
      .send
        line-height 40px
        text-align center
        background red
        a
          color #fff
</style>



