<template>
  <div class="hello">
    <Scroll ref="scroll">
      <div>
        <Background>
          <Heade :data="hdInfo"></Heade>
          <div class="hello-wrapper">
            <div class="hello-content">
              <div class="text">
                <div class="notice">{{info.notice}}</div>
                <div class="description">{{info.description}}</div>
              </div>
              <div class="etermine">
                <span @click="timerOut()">
                  <div class="mine-pi">
                    <span v-show="span"></span>
                  </div>
                  <div class="mine-text">{{info.mine_text}}</div>
                </span>
              </div>
            </div>
            <div class="hello-buttons">
              <router-link to="/problem" tag="div" v-if="showReport" class="go">{{info.go_report}}</router-link>
              <div v-if="showInterv" class="dis go">{{time}}</div>
              <router-link to="/queries" tag="div" class="go">{{info.go_query}}</router-link>
            </div>
          </div>
        </Background>
      </div>
    </Scroll>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
import Heade from 'components/Heade'
import Background from 'components/Background'
import Scroll from 'base/Scroll'

// 默认的文本
const DEFAULT_INFO = {
  notice: '举报须知',
  description: '  本系统受理反应“四风”问题举报，具体包括公款大吃大喝，公款旅游（国内）、公款出国（境）旅游、违规配备使用公车、违规建设搂堂馆所、津贴补贴违规问题、违规收受礼品礼金、大办婚丧喜庆事宜、提供或者接受超标准接待、接受或用公款参与高消费娱乐健身活动、违规出入私人会所、培训疗养机构“四风”问题',
  mine_text: '同意以上条款',
  go_report: '我要举报',
  go_query: '举报查询'
}

// header文本的内容
const TITEL_INFO = {
  big_title: '芙蓉区纪委监察局',
  small_title: '党风政风监督举报平台'
}
export default {
  components: {
    Heade,
    Background,
    Scroll
  },
  data() {
    return {
      span: false,
      info: DEFAULT_INFO, // 基本信息
      hdInfo: TITEL_INFO,
      time: '先同意协议',
      showReport: false,
      showInterv: true
    }
  },
  methods: {
    timerOut() {
      this.span = true
      let i = 5
      let time = setInterval(() => {
        if (i > 0) {
          this.time = i-- + ' 秒'
        } else {
          this.showInterv = false
          this.showReport = true
          clearInterval(time)
        }
      }, 1000)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  .hello
    position fixed
    top 0px
    left 0px
    bottom 0px
    right 0px
    padding-bottom 0px
    .hello-wrapper
      padding 15px
      .hello-content
        font-size 14px
        .text
          background #fff
          padding 18px
          border-radius 0px
          .notice
            font-size 18px
            text-align center
            margin-bottom 18px
            font-weight 700
            color red
          .description
            text-indent:2em
            line-height 25px
        .etermine
          padding 20px 0
          text-align center
          div
            display inline-block
          .mine-pi
            vertical-align top
            width 16px
            height 16px
            border 2px solid red
            border-radius 50%
            margin-right 5px
            span
              display inline-block
              margin 2px
              width 12px
              height 12px
              border-radius 50%
              background red;
          .mine-text
            vertical-align top
            line-height 20px
      .hello-buttons
        line-height 20px;
        .go 
          display block
          background red
          color #fff
          border-radius 0px
          height 45px
          line-height 45px
          margin-bottom 20px
          text-align center
          &.dis
            background red
            color #fff
</style>



